#createQuestion {
  width: 50em;
  position: fixed;
  display: -webkit-inline-box;
  z-index: 1;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  .form4 {
    background-size: cover;
  }

  .top {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  .form-bg {
    padding: 20px;
    margin-left: 40px;
  }

  .form {
    background-color: white !important;
    padding: 20px;
  }

  .form-group {
    margin-bottom: 20px;
  }

  .sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
  }

  .form-control {
    border-top: 0px;
    border-left: 0px;
    border-right: 0px;
    border-bottom: 1px solid #61615e;
    background: transparent;
    -webkit-transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
  }

  button.btn {
    display: block;
    padding: 11px 32px;
    -webkit-transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    margin: 30px 0px;
  }

  .btn-blue {
    background-color: #618ab8;
    color: #fff;
    outline: none !important;
    box-shadow: none !important;

    &:hover {
      background-color: #063970;
      color: #fff;
    }
  }

  .x-btn {
    outline: none !important;
    box-shadow: none !important;
    background-color: #fff;
    border: none !important;
  }
}
