  button {
    border: none;
    padding: 0;
    font-size: 0;
    line-height: 0;
    background: none;
    cursor: pointer;
  }

  :focus {
    outline: 0;
  }

  .clearfix:before,
  .clearfix:after {
    content: "\0020";
    display: block;
    height: 0;
    visibility: hidden;
  }

  .clearfix:after {
    clear: both;
  }

  .clearfix {
    zoom: 1;
  }

